:root {
  --bg-gradient-onyx: linear-gradient( to bottom right, #3f3f40 3%, #303030 97% );
  --bg-gradient-jet: linear-gradient( to bottom right, #2d2d2e40 0%, #1b1b1d00 100% ), #202022;
  --bg-gradient-yellow-1: linear-gradient( to bottom right, #ffda6b 0%, #ffc06100 50% );
  --bg-gradient-yellow-2: linear-gradient( 135deg, #ffda6b40 0%, #ffbb5c00 59.86% ), #202022;
  --border-gradient-onyx: linear-gradient( to bottom right, #404040 0%, #40404000 50% );
  --text-gradient-yellow: linear-gradient( to right, #ffdb70, #ffbb5c );
  --jet: #383838;
  --onyx: #2b2b2c;
  --eerie-black-1: #202022;
  --eerie-black-2: #1e1e1f;
  --smoky-black: #121212;
  --white-1: #fff;
  --white-2: #fafafa;
  --orange-yellow-crayola: #ffdb70;
  --vegas-gold: #ceb15a;
  --light-gray: #d6d6d6;
  --light-gray-70: #d6d6d6b3;
  --bittersweet-shimmer: #b84c4c;
  --ff-poppins: "Poppins", sans-serif;
  --fs-1: 24px;
  --fs-2: 18px;
  --fs-3: 17px;
  --fs-4: 16px;
  --fs-5: 15px;
  --fs-6: 14px;
  --fs-7: 13px;
  --fs-8: 11px;
  --fw-300: 300;
  --fw-400: 400;
  --fw-500: 500;
  --fw-600: 600;
  --shadow-1: -4px 8px 24px #00000040;
  --shadow-2: 0 16px 30px #00000040;
  --shadow-3: 0 16px 40px #00000040;
  --shadow-4: 0 25px 50px #00000026;
  --shadow-5: 0 24px 80px #00000040;
  --transition-1: .25s ease;
  --transition-2: .5s ease-in-out;
}

*, :before, :after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
}

li {
  list-style: none;
}

img, ion-icon, a, button, time, span {
  display: block;
}

button {
  font: inherit;
  text-align: left;
  cursor: pointer;
  background: none;
  border: none;
}

input, textarea {
  width: 100%;
  font: inherit;
  background: none;
  display: block;
}

::selection {
  background: var(--orange-yellow-crayola);
  color: var(--smoky-black);
}

:focus {
  outline-color: var(--orange-yellow-crayola);
}

html {
  font-family: var(--ff-poppins);
}

body {
  background: var(--smoky-black);
}

.sidebar, article {
  background: var(--eerie-black-2);
  border: 1px solid var(--jet);
  box-shadow: var(--shadow-1);
  z-index: 1;
  border-radius: 20px;
  padding: 15px;
}

.separator {
  width: 100%;
  height: 1px;
  background: var(--jet);
  margin: 16px 0;
}

.icon-box {
  background: var(--border-gradient-onyx);
  width: 30px;
  height: 30px;
  color: var(--orange-yellow-crayola);
  box-shadow: var(--shadow-1);
  z-index: 1;
  border-radius: 8px;
  justify-content: center;
  align-items: center;
  font-size: 16px;
  display: flex;
  position: relative;
}

.icon-box:before {
  content: "";
  background: var(--eerie-black-1);
  border-radius: inherit;
  z-index: -1;
  position: absolute;
  inset: 1px;
}

.icon-box ion-icon {
  --ionicon-stroke-width: 35px;
}

article {
  display: none;
}

article.active {
  animation: .5s backwards fade;
  display: block;
}

@keyframes fade {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

.h2, .h3, .h4, .h5 {
  color: var(--white-2);
  text-transform: capitalize;
}

.h2 {
  font-size: var(--fs-1);
}

.h3 {
  font-size: var(--fs-2);
}

.h4 {
  font-size: var(--fs-4);
}

.h5 {
  font-size: var(--fs-7);
  font-weight: var(--fw-500);
}

.article-title {
  padding-bottom: 7px;
  position: relative;
}

.article-title:after {
  content: "";
  width: 30px;
  height: 3px;
  background: var(--text-gradient-yellow);
  border-radius: 3px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.has-scrollbar::-webkit-scrollbar {
  width: 5px;
  height: 5px;
}

.has-scrollbar::-webkit-scrollbar-track {
  background: var(--onyx);
  border-radius: 5px;
}

.has-scrollbar::-webkit-scrollbar-thumb {
  background: var(--orange-yellow-crayola);
  border-radius: 5px;
}

.has-scrollbar::-webkit-scrollbar-button {
  width: 20px;
}

.content-card {
  background: var(--border-gradient-onyx);
  box-shadow: var(--shadow-2);
  cursor: pointer;
  z-index: 1;
  border-radius: 14px;
  padding: 45px 15px 15px;
  position: relative;
}

.content-card:before {
  content: "";
  background: var(--bg-gradient-jet);
  border-radius: inherit;
  z-index: -1;
  position: absolute;
  inset: 1px;
}

main {
  min-width: 259px;
  margin: 15px 12px 75px;
}

.sidebar {
  max-height: 112px;
  transition: var(--transition-2);
  margin-bottom: 15px;
  overflow: hidden;
}

.sidebar.active {
  max-height: 405px;
}

.sidebar-info {
  justify-content: flex-start;
  align-items: center;
  gap: 15px;
  display: flex;
  position: relative;
}

.avatar-box {
  background: var(--bg-gradient-onyx);
  border-radius: 20px;
}

.info-content .name {
  color: var(--white-2);
  font-size: var(--fs-3);
  font-weight: var(--fw-500);
  letter-spacing: -.25px;
  margin-bottom: 10px;
}

.info-content .title {
  color: var(--white-1);
  background: var(--onyx);
  font-size: var(--fs-8);
  font-weight: var(--fw-300);
  width: max-content;
  border-radius: 8px;
  padding: 3px 12px;
}

.info_more-btn {
  color: var(--orange-yellow-crayola);
  background: var(--border-gradient-onyx);
  box-shadow: var(--shadow-2);
  transition: var(--transition-1);
  z-index: 1;
  border-radius: 0 15px;
  padding: 10px;
  font-size: 13px;
  position: absolute;
  top: -15px;
  right: -15px;
}

.info_more-btn:before {
  content: "";
  border-radius: inherit;
  background: var(--bg-gradient-jet);
  transition: var(--transition-1);
  z-index: -1;
  position: absolute;
  inset: 1px;
}

.info_more-btn:hover, .info_more-btn:focus {
  background: var(--bg-gradient-yellow-1);
}

.info_more-btn:hover:before, .info_more-btn:focus:before {
  background: var(--bg-gradient-yellow-2);
}

.info_more-btn span {
  display: none;
}

.sidebar-info_more {
  opacity: 0;
  visibility: hidden;
  transition: var(--transition-2);
}

.sidebar.active .sidebar-info_more {
  opacity: 1;
  visibility: visible;
}

.contacts-list {
  grid-template-columns: 1fr;
  gap: 16px;
  display: grid;
}

.contact-item {
  min-width: 100%;
  align-items: center;
  gap: 16px;
  display: flex;
}

.contact-info {
  max-width: calc(100% - 46px);
  width: calc(100% - 46px);
}

.contact-title {
  color: var(--light-gray-70);
  font-size: var(--fs-8);
  text-transform: uppercase;
  margin-bottom: 2px;
}

.contact-info :is(.contact-link, time, address) {
  color: var(--white-2);
  font-size: var(--fs-7);
}

.contact-info address {
  font-style: normal;
}

.social-list {
  justify-content: flex-start;
  align-items: center;
  gap: 15px;
  padding-bottom: 4px;
  padding-left: 7px;
  display: flex;
}

.social-item .social-link {
  color: var(--light-gray-70);
  font-size: 18px;
}

.social-item .social-link:hover {
  color: var(--light-gray);
}

.navbar {
  width: 100%;
  -webkit-backdrop-filter: blur(10px);
  backdrop-filter: blur(10px);
  border: 1px solid var(--jet);
  box-shadow: var(--shadow-2);
  z-index: 5;
  background: #2b2b2cbf;
  border-radius: 12px 12px 0 0;
  position: fixed;
  bottom: 0;
  left: 0;
}

.navbar-list {
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  padding: 0 10px;
  display: flex;
}

.navbar-link {
  color: var(--light-gray);
  font-size: var(--fs-8);
  transition: color var(--transition-1);
  padding: 20px 7px;
}

.navbar-link:hover, .navbar-link:focus {
  color: var(--light-gray-70);
}

.navbar-link.active {
  color: var(--orange-yellow-crayola);
}

.about .article-title {
  margin-bottom: 15px;
}

.about-text {
  color: var(--light-gray);
  font-size: var(--fs-6);
  font-weight: var(--fw-300);
  line-height: 1.6;
}

.about-text p {
  margin-bottom: 15px;
}

.service {
  margin-bottom: 35px;
}

.service-title {
  margin-bottom: 20px;
}

.service-list {
  grid-template-columns: 1fr;
  gap: 20px;
  display: grid;
}

.service-item {
  background: var(--border-gradient-onyx);
  box-shadow: var(--shadow-2);
  z-index: 1;
  border-radius: 14px;
  padding: 20px;
  position: relative;
}

.service-item:before {
  content: "";
  background: var(--bg-gradient-jet);
  border-radius: inherit;
  z-index: -1;
  position: absolute;
  inset: 1px;
}

.service-icon-box {
  margin-bottom: 10px;
}

.service-icon-box img {
  margin: auto;
}

.service-content-box {
  text-align: center;
}

.service-item-title {
  margin-bottom: 7px;
}

.service-item-text {
  color: var(--light-gray);
  font-size: var(--fs-6);
  font-weight: var(--fw-3);
  line-height: 1.6;
}

.testimonials {
  margin-bottom: 30px;
}

.testimonials-title {
  margin-bottom: 20px;
}

.testimonials-list {
  scroll-behavior: smooth;
  overscroll-behavior-inline: contain;
  scroll-snap-type: inline mandatory;
  justify-content: flex-start;
  align-items: flex-start;
  gap: 15px;
  margin: 0 -15px;
  padding: 25px 15px 35px;
  display: flex;
  overflow-x: auto;
}

.testimonials-item {
  min-width: 100%;
  scroll-snap-align: center;
}

.testimonials-avatar-box {
  background: var(--bg-gradient-onyx);
  box-shadow: var(--shadow-1);
  border-radius: 14px;
  position: absolute;
  top: 0;
  left: 0;
  transform: translate(15px, -25px);
}

.testimonials-item-title {
  margin-bottom: 7px;
}

.testimonials-text {
  color: var(--light-gray);
  font-size: var(--fs-6);
  font-weight: var(--fw-300);
  line-clamp: 4;
  -webkit-line-clamp: 4;
  -webkit-box-orient: vertical;
  line-height: 1.6;
  display: -webkit-box;
  overflow: hidden;
}

.modal-container {
  width: 100%;
  height: 100%;
  overscroll-behavior: contain;
  z-index: 20;
  pointer-events: none;
  visibility: hidden;
  justify-content: center;
  align-items: center;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: auto;
}

.modal-container::-webkit-scrollbar {
  display: none;
}

.modal-container.active {
  pointer-events: all;
  visibility: visible;
}

.overlay {
  width: 100%;
  height: 100vh;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  z-index: 1;
  transition: var(--transition-1);
  background: #0d0d0d;
  position: fixed;
  top: 0;
  left: 0;
}

.overlay.active {
  opacity: .8;
  visibility: visible;
  pointer-events: all;
}

.testimonials-modal {
  background: var(--eerie-black-2);
  border: 1px solid var(--jet);
  box-shadow: var(--shadow-5);
  opacity: 0;
  transition: var(--transition-1);
  z-index: 2;
  border-radius: 14px;
  margin: 15px 12px;
  padding: 15px;
  position: relative;
  transform: scale(1.2);
}

.modal-container.active .testimonials-modal {
  opacity: 1;
  transform: scale(1);
}

.modal-close-btn {
  background: var(--onyx);
  width: 32px;
  height: 32px;
  color: var(--white-2);
  opacity: .7;
  border-radius: 8px;
  justify-content: center;
  align-items: center;
  font-size: 18px;
  display: flex;
  position: absolute;
  top: 15px;
  right: 15px;
}

.modal-close-btn:hover, .modal-close-btn:focus {
  opacity: 1;
}

.modal-close-btn ion-icon {
  --ionicon-stroke-width: 50px;
}

.modal-avatar-box {
  background: var(--bg-gradient-onyx);
  width: max-content;
  box-shadow: var(--shadow-2);
  border-radius: 14px;
  margin-bottom: 15px;
}

.modal-img-wrapper > img {
  display: none;
}

.modal-title {
  margin-bottom: 4px;
}

.modal-content time {
  font-size: var(--fs-6);
  color: var(--light-gray-70);
  font-weight: var(--fw-300);
  margin-bottom: 10px;
}

.modal-content p {
  color: var(--light-gray);
  font-size: var(--fs-6);
  font-weight: var(--fw-300);
  line-height: 1.6;
}

.clients {
  margin-bottom: 15px;
}

.clients-list {
  scroll-behavior: smooth;
  overscroll-behavior-inline: contain;
  scroll-snap-type: inline mandatory;
  justify-content: flex-start;
  align-items: flex-start;
  gap: 15px;
  margin: 0 -15px;
  padding: 25px;
  scroll-padding-inline: 25px;
  display: flex;
  overflow-x: auto;
}

.clients-item {
  min-width: 50%;
  scroll-snap-align: start;
}

.clients-item img {
  width: 100%;
  filter: grayscale();
  transition: var(--transition-1);
}

.clients-item img:hover {
  filter: grayscale(0);
}

.article-title, .timeline {
  margin-bottom: 30px;
}

.timeline .title-wrapper {
  align-items: center;
  gap: 15px;
  margin-bottom: 25px;
  display: flex;
}

.timeline-list {
  font-size: var(--fs-6);
  margin-left: 45px;
}

.timeline-item {
  position: relative;
}

.timeline-item:not(:last-child) {
  margin-bottom: 20px;
}

.timeline-item-title {
  font-size: var(--fs-6);
  margin-bottom: 7px;
  line-height: 1.3;
}

.timeline-list span {
  color: var(--vegas-gold);
  font-weight: var(--fw-400);
  line-height: 1.6;
}

.timeline-item:not(:last-child):before {
  content: "";
  width: 1px;
  height: calc(100% + 50px);
  background: var(--jet);
  position: absolute;
  top: -25px;
  left: -30px;
}

.timeline-item:after {
  content: "";
  height: 6px;
  width: 6px;
  background: var(--text-gradient-yellow);
  box-shadow: 0 0 0 4px var(--jet);
  border-radius: 50%;
  position: absolute;
  top: 5px;
  left: -33px;
}

.timeline-text {
  color: var(--light-gray);
  font-weight: var(--fw-300);
  line-height: 1.6;
}

.skills-title {
  margin-bottom: 20px;
}

.skills-list {
  padding: 20px;
}

.skills-item:not(:last-child) {
  margin-bottom: 15px;
}

.skill .title-wrapper {
  align-items: center;
  gap: 5px;
  margin-bottom: 8px;
  display: flex;
}

.skill .title-wrapper data {
  color: var(--light-gray);
  font-size: var(--fs-7);
  font-weight: var(--fw-300);
}

.skill-progress-bg {
  background: var(--jet);
  width: 100%;
  height: 8px;
  border-radius: 10px;
}

.skill-progress-fill {
  background: var(--text-gradient-yellow);
  height: 100%;
  border-radius: inherit;
}

.filter-list {
  display: none;
}

.filter-select-box {
  margin-bottom: 25px;
  position: relative;
}

.filter-select {
  background: var(--eerie-black-2);
  color: var(--light-gray);
  width: 100%;
  border: 1px solid var(--jet);
  font-size: var(--fs-6);
  font-weight: var(--fw-300);
  border-radius: 14px;
  justify-content: space-between;
  align-items: center;
  padding: 12px 16px;
  display: flex;
}

.filter-select.active .select-icon {
  transform: rotate(.5turn);
}

.select-list {
  background: var(--eerie-black-2);
  width: 100%;
  border: 1px solid var(--jet);
  z-index: 2;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  border-radius: 14px;
  padding: 6px;
  transition: all .15s ease-in-out;
  position: absolute;
  top: calc(100% + 6px);
}

.filter-select.active + .select-list {
  opacity: 1;
  visibility: visible;
  pointer-events: all;
}

.select-item button {
  background: var(--eerie-black-2);
  color: var(--light-gray);
  font-size: var(--fs-6);
  font-weight: var(--fw-300);
  text-transform: capitalize;
  width: 100%;
  border-radius: 8px;
  padding: 8px 10px;
}

.select-item button:hover {
  --eerie-black-2: #323234;
}

.project-list {
  grid-template-columns: 1fr;
  gap: 30px;
  margin-bottom: 10px;
  display: grid;
}

.project-item {
  display: none;
}

.project-item.active {
  animation: .25s forwards scaleUp;
  display: block;
}

@keyframes scaleUp {
  0% {
    transform: scale(.5);
  }

  100% {
    transform: scale(1);
  }
}

.project-item > a {
  width: 100%;
}

.project-img {
  width: 100%;
  height: 200px;
  border-radius: 16px;
  margin-bottom: 15px;
  position: relative;
  overflow: hidden;
}

.project-img:before {
  content: "";
  width: 100%;
  height: 100%;
  z-index: 1;
  transition: var(--transition-1);
  background: none;
  position: absolute;
  top: 0;
  left: 0;
}

.project-item > a:hover .project-img:before {
  background: #00000080;
}

.project-item-icon-box {
  --scale: .8;
  background: var(--jet);
  color: var(--orange-yellow-crayola);
  transform: translate(-50%, -50%) scale(var(--scale));
  opacity: 0;
  z-index: 1;
  transition: var(--transition-1);
  border-radius: 12px;
  padding: 18px;
  font-size: 20px;
  position: absolute;
  top: 50%;
  left: 50%;
}

.project-item > a:hover .project-item-icon-box {
  --scale: 1;
  opacity: 1;
}

.project-item-icon-box ion-icon {
  --ionicon-stroke-width: 50px;
}

.project-img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: var(--transition-1);
}

.project-item > a:hover img {
  transform: scale(1.1);
}

.project-title, .project-category {
  margin-left: 10px;
}

.project-title {
  color: var(--white-2);
  font-size: var(--fs-5);
  font-weight: var(--fw-400);
  text-transform: capitalize;
  line-height: 1.3;
}

.project-category {
  color: var(--light-gray-70);
  font-size: var(--fs-6);
  font-weight: var(--fw-300);
}

.blog-posts {
  margin-bottom: 10px;
}

.blog-posts-list {
  grid-template-columns: 1fr;
  gap: 20px;
  display: grid;
}

.blog-post-item > a {
  background: var(--border-gradient-onyx);
  height: 100%;
  box-shadow: var(--shadow-4);
  z-index: 1;
  border-radius: 16px;
  position: relative;
}

.blog-post-item > a:before {
  content: "";
  border-radius: inherit;
  background: var(--eerie-black-1);
  z-index: -1;
  position: absolute;
  inset: 1px;
}

.blog-banner-box {
  width: 100%;
  height: 200px;
  border-radius: 12px;
  overflow: hidden;
}

.blog-banner-box img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: var(--transition-1);
}

.blog-post-item > a:hover .blog-banner-box img {
  transform: scale(1.1);
}

.blog-content {
  padding: 15px;
}

.blog-meta {
  justify-content: flex-start;
  align-items: center;
  gap: 7px;
  margin-bottom: 10px;
  display: flex;
}

.blog-meta :is(.blog-category, time) {
  color: var(--light-gray-70);
  font-size: var(--fs-6);
  font-weight: var(--fw-300);
}

.blog-meta .dot {
  background: var(--light-gray-70);
  width: 4px;
  height: 4px;
  border-radius: 4px;
}

.blog-item-title {
  transition: var(--transition-1);
  margin-bottom: 10px;
  line-height: 1.3;
}

.blog-post-item > a:hover .blog-item-title {
  color: var(--orange-yellow-crayola);
}

.blog-text {
  color: var(--light-gray);
  font-size: var(--fs-6);
  font-weight: var(--fw-300);
  line-height: 1.6;
}

.mapbox {
  height: 250px;
  width: 100%;
  border: 1px solid var(--jet);
  border-radius: 16px;
  margin-bottom: 30px;
  position: relative;
  overflow: hidden;
}

.mapbox figure {
  height: 100%;
}

.mapbox iframe {
  width: 100%;
  height: 100%;
  filter: grayscale() invert();
  border: none;
}

.contact-form {
  margin-bottom: 10px;
}

